import React, { useRef, useState, useEffect } from "react";
import { Button, TextField, Autocomplete } from "@mui/material";
import "../App.css";

const AddTodoForm = ({
    setShowForm,
    handleAddTodo,
    editable,
    setEditable,
    fetchData,
    clickedTodo,
    setClickedTodo,
    setNotification,
}) => {
    const [todo, setTodo] = useState({
        title: "",
        description: "",
        priority: "",
        dueDate: "",
        completed: false,
    });
    const [suggestions] = useState(["low", "medium", "high"]);
    useEffect(() => {
        if (editable) {
            setTodo(clickedTodo);
        }
    }, [editable, clickedTodo]);
    const handleChange = (e) => {
        var { name, value } = e.target;
        setTodo((prevTodo) => ({ ...prevTodo, [name]: value }));
    };
    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(todo);
        if (editable) {
            localStorage.removeItem(clickedTodo.title);
            localStorage.setItem(todo.title, JSON.stringify(todo));
            fetchData();
            setNotification("Todo updated successfully.");
            setTimeout(() => {
                setNotification(null);
            }, 3000);
            setClickedTodo(null);
            setEditable(false);
            setShowForm(false);
        } else {
            localStorage.setItem(todo.title, JSON.stringify(todo));
            handleAddTodo(todo);
            setNotification("Todo created successfully.");
            setTimeout(() => {
                setNotification(null);
            }, 3000);
        }

        setTodo({
            title: "",
            description: "",
            priority: "",
            dueDate: "",
        });
    };
    const closeRef = useRef();
    const closeForm = () => {
        setShowForm(false);
        setClickedTodo(null);
        setEditable(false);
    };
    return (
        <div className="add-todo-form">
            <div
                className="close-form"
                ref={closeRef}
                style={{ fontSize: "40px", float: "right", cursor: "pointer" }}
                onClick={closeForm}
            >
                &times;
            </div>
            <form onSubmit={handleSubmit}>
                <TextField
                    name="title"
                    value={todo.title}
                    onChange={handleChange}
                    size="small"
                    label="Title"
                    variant="outlined"
                    fullWidth
                    margin="dense"
                    required
                    aria-required
                />
                <TextField
                    name="description"
                    value={todo.description}
                    onChange={handleChange}
                    size="small"
                    label="Description"
                    variant="outlined"
                    fullWidth
                    multiline
                    margin="dense"
                    required
                />
                <Autocomplete
                    value={todo.priority}
                    onChange={(event, newValue) => {
                        handleChange({
                            target: { name: "priority", value: newValue },
                        });
                    }}
                    options={suggestions}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            name="priority"
                            label="Priority"
                            variant="outlined"
                            size="small"
                            fullWidth
                            margin="dense"
                            required
                        />
                    )}
                />
                <TextField
                    type="date"
                    name="dueDate"
                    value={todo.dueDate}
                    onChange={handleChange}
                    size="small"
                    label="Due Date"
                    variant="outlined"
                    fullWidth
                    InputLabelProps={{
                        shrink: true,
                    }}
                    margin="dense"
                    required
                />
                <Button variant="contained" type="submit" fullWidth>
                    {editable ? "Update Todo" : "Add Todo"}
                </Button>
            </form>
        </div>
    );
};

export default AddTodoForm;
