import React, { useState } from "react";
import { Button } from "@mui/material";

const TodoItem = ({ todo, showItem, setShowItem }) => {
    const [completed, setCompleted] = useState(todo.completed);
    const handleTodo = () => {
        setCompleted((bool) => !bool);
        todo.completed = !todo.completed;
        localStorage.setItem(todo.title, JSON.stringify(todo));
    };
    const closeTodoItem = () => {
        setShowItem(false);
    };
    return (
        showItem && (
            <div className="todo-item">
                <div className="close-form" onClick={closeTodoItem}>
                    &times;
                </div>
                <div className="todo-data">
                    <h2>{todo.title}</h2>
                    <Button
                        onClick={handleTodo}
                        variant="contained"
                        color={todo.completed ? "error" : "success"}
                        size="small"
                    >
                        Mark as {todo.completed ? "Incomplete" : "Complete"}
                    </Button>
                    <p><b>Due Date :</b> {todo.dueDate}</p>
                    <p><b>Priority :</b> {todo.priority} </p>
                    <p><b>Status :</b> {completed ? "Completed" : "Pending"} </p>
                    <p><b><u>Description</u></b></p>
                    <p className="description"> <i>{todo.description}</i></p>
                </div>
            </div>
        )
    );
};

export default TodoItem;
