import React, { useState } from "react";
import TodoItem from "./TodoItem";
import { Button } from "@mui/material";

const TodoList = ({
    sortedTodos,
    fetchData,
    setEditable,
    setShowForm,
    clickedTodo,
    setClickedTodo,
    setNotification,
}) => {
    const [showItem, setShowItem] = useState(false);
    const handleView = (todo) => {
        setClickedTodo(todo);
        setShowItem(true);
    };
    const handleDelete = (todo) => {
        localStorage.removeItem(todo.title);
        fetchData();
        setClickedTodo(null);
        setNotification("Todo deleted successfully.");
        setTimeout(() => {
            setNotification(null);
        }, 3000);
    };
    const handleEdit = (todo) => {
        setEditable(true);
        setShowForm(true);
        setClickedTodo(todo);
    };
    return (
        <div className="todo-container">
            <div className="todo-list">
                {sortedTodos.map((todo) => (
                    <div key={todo.title}>
                        <span>{todo.title}</span>
                        <Button
                            variant="contained"
                            color="info"
                            onClick={() => handleView(todo)}
                        >
                            View
                        </Button>
                        <Button
                            variant="contained"
                            color="success"
                            onClick={() => handleEdit(todo)}
                        >
                            Edit
                        </Button>
                        <Button
                            variant="contained"
                            color="error"
                            onClick={() => handleDelete(todo)}
                        >
                            Delete
                        </Button>
                    </div>
                ))}
            </div>
            <div>
                {clickedTodo && (
                    <TodoItem
                        todo={clickedTodo}
                        showItem={showItem}
                        setShowItem={setShowItem}
                    />
                )}
            </div>
        </div>
    );
};

export default TodoList;
