import React, { useCallback, useEffect, useState } from "react";
import {
    Button,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
} from "@mui/material";

const FilterTodos = ({ todoData, setSortedTodos, setClickedTodo }) => {
    const [sortPriority, setSortPriority] = useState("");
    const handleCompleted = () => {
        setClickedTodo(null);
        if (sortPriority === "" || sortPriority === "all") {
            setSortedTodos(todoData.filter((todo) => todo.completed === true));
        } else {
            setSortedTodos(
                todoData.filter(
                    (todo) =>
                        todo.completed === true &&
                        todo.priority === sortPriority
                )
            );
        }
    };
    const handleAll = () => {
        setClickedTodo(null);
        if (sortPriority === "" || sortPriority === "all") {
            setSortedTodos(todoData);
        } else {
            setSortedTodos(
                todoData.filter((todo) => todo.priority === sortPriority)
            );
        }
    };
    const handlePending = () => {
        setClickedTodo(null);
        if (sortPriority === "" || sortPriority === "all") {
            setSortedTodos(todoData.filter((todo) => todo.completed !== true));
        } else {
            setSortedTodos(
                todoData.filter(
                    (todo) =>
                        todo.completed !== true &&
                        todo.priority === sortPriority
                )
            );
        }
    };
    const sortTodos = useCallback(
        (sortPriority) => {
            if (sortPriority === "high") {
                return todoData.filter((todo) => todo.priority === "high");
            } else if (sortPriority === "medium") {
                return todoData.filter((todo) => todo.priority === "medium");
            } else if (sortPriority === "low") {
                return todoData.filter((todo) => todo.priority === "low");
            } else {
                return todoData;
            }
        },
        [todoData]
    );
    useEffect(() => {
        let sortedTodos = sortTodos(sortPriority);
        setSortedTodos(sortedTodos);
        setClickedTodo(null);
    }, [setClickedTodo, setSortedTodos, sortTodos, sortPriority]);
    return (
        <div className="filter-todos">
            <FormControl sx={{ m: 1, minWidth: 120 }}>
                <InputLabel id="demo-simple-select-helper-label">
                    Priority
                </InputLabel>
                <Select
                    labelId="demo-simple-select-helper-label"
                    label="Sort by Priority"
                    id="priority"
                    value={sortPriority}
                    onChange={(e) => setSortPriority(e.target.value)}
                    size="small"
                >
                    <MenuItem value="all">
                        <em>All</em>
                    </MenuItem>
                    <MenuItem value="low">Low</MenuItem>
                    <MenuItem value="medium">Medium</MenuItem>
                    <MenuItem value="high">High</MenuItem>
                </Select>
            </FormControl>
            <Button onClick={handleAll} variant="contained" size="small">
                All
            </Button>
            <Button onClick={handlePending} variant="contained" size="small">
                Pending
            </Button>
            <Button onClick={handleCompleted} variant="contained" size="small">
                Completed
            </Button>
        </div>
    );
};

export default FilterTodos;
